html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  background-color: #ebeaea;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  font-weight: 500;
  stop-color: #000c2b;
  margin: 0;
  text-align: justify;
  color: #000c2b;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  margin: 0 0 5rem 0;
  cursor: default;
  font-weight: 700;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4.5rem;
}
@media (max-width: 600px) {
  h2 {
    font-size: 4rem !important;
  }
}

h3 {
  font-size: 4rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h2 {
  font-size: 5.5rem;
  font-weight: 800;
}

.border {
  border-bottom: 0.5rem solid #d21314;
}

.text {
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 90rem;
}

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.5% 0;
  background-color: #06065c;
}
header .logo {
  width: 8%;
}
header nav {
  display: flex;
  align-items: center;
  gap: 15%;
  margin-right: 5%;
}
header nav a {
  color: #ebeaea;
  font-size: 2rem;
  height: 2.5rem;
  transition: all 0.4s;
}
header nav a:hover {
  border-bottom: 0.4rem solid #ebeaea;
}
header .menu {
  display: none;
}
header .active {
  border-bottom: 0.4rem solid #ebeaea;
}

.menu-hide a {
  transition: all ease 0.5s;
}

.menu-hide a:hover {
  background-color: #ebeaea;
  color: #d21314;
}

.active-hide {
  background-color: #ebeaea;
  color: #d21314 !important;
}

@media (max-width: 900px) {
  .logo {
    width: 12rem !important;
    height: 6rem;
  }
  .menu {
    display: block !important;
    border-radius: 10px;
    cursor: pointer;
  }
  .menu:hover {
    background-color: lightgray;
  }
  .menu-hide {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #06065c;
    width: 100%;
    padding: 0.5% 0;
  }
  .menu-hide ul {
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .menu-hide li {
    list-style: none;
    width: 100%;
  }
  .nav_a-hide {
    width: 100%;
  }
  .menu-hide a {
    display: block;
    text-decoration: none;
    width: 100%;
    padding: 3% 0;
    font-size: x-large;
    font-weight: 700;
    color: #ebeaea;
  }
  header nav {
    display: none !important;
  }
}
