.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 12, 43, 0.5);
  display: none;
}
.modal .modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 80%;
  padding: 5% 8%;
  opacity: 0;
  transition: all 0.7s;
}
.modal .modal-content img {
  height: 22rem;
  width: 60rem;
  margin: 0 auto;
  border-radius: 10px;
}
@media (max-width: 900px) {
  .modal .modal-content img {
    height: 10rem;
    width: 30rem;
  }
}
.modal .modal-content h2 {
  color: #d21314;
  margin: 0;
}
@media (max-width: 900px) {
  .modal .modal-content h2 {
    font-size: 2.5rem !important;
  }
}
.modal .modal-content p {
  display: block;
  color: #ebeaea;
  font-size: 2rem;
}
@media (max-width: 900px) {
  .modal .modal-content p {
    font-size: 1.5rem;
  }
}
.modal .modal-close {
  color: #000c2b;
  background-color: #ebeaea;
  border: none;
  border-radius: 50%;
  font-size: 3rem;
  font-weight: 700;
  padding: 0.5% 1%;
  position: absolute;
  left: 48.5%;
  top: 90%;
  cursor: pointer;
  transition: all 0.5s;
}
.modal .modal-close:hover {
  opacity: 0.8;
}

